<template>
<van-dialog v-model:show="show" title="Editor Window" :showConfirmButton="true" :closeOnClickOverlay="true" width="640" :showCancelButton="true" @confirm="confirm()">
    <div class="p-2 h-96">
        <textarea type="textArea" class="border w-full h-48" v-model="userText"></textarea>
    </div>
</van-dialog>
</template>

<script>
import Processor from "@/logics/lessonProcessor.js";
import {
    ref
} from "vue";
import {
    Dialog as VanDialog,
    Button
} from "vant";

export default {
    components: {
        [Button.name]: Button,
        [VanDialog.name]: VanDialog
    },
    setup(props, {
        emit
    }) {
        const show = ref(false);
        const userText = ref("");
        let theNode = null;

        const showDialog = function (node) {
            theNode = node;
            userText.value = node.content;
            show.value = true;
        };

        const confirm = function () {
            theNode.content = userText.value;
            if (theNode.varName) {
                Processor.store.set(theNode.varName, userText.value);
            }
            show.value = false;
            if (!userText.value) {
                emit("removeNode");
            }
        };

        return {
            show,
            userText,
            confirm,
            showDialog
        };
    }
}
</script>
